import pubfooter from '@/components/pubfooter/pubfooter'
import slideryouxue from '@/base/slideryouxue/slideryouxue'
import sliderbigyx from '@/base/sliderbigyx/sliderbigyx'
import topsearch from '@/base/topsearch/topsearch'
import axios from 'axios'

export default {
  name: 'abroad',
  data: function () {
    return {
      tabIndex: 0,
      sliderName: [],
      dataList: {},
      lxqz: [],
      lxbb: [],
      guojiaIndex: 0
    }
  },
  created() {
    this.$store.commit('LOADING_SHOW', true)
    this._getGoodList()
    this._getAbroad()
  },
  methods: {
    goEquipDetail(id) {
      this.$router.push({path: 'equipdetails', query: { id: id }})
    },
    gocountry(id) {
      // this.$store.commit('LOADING_SHOW', true)
      this.dataList.categorydata = {}
      if (this.guojiaIndex === id) {
        this.guojiaIndex = 0
        this._getGoodList()
        // this.changeTab()
      } else {
        this.guojiaIndex = id
        this._getGoodList(id)
        // this.changeTab()
      }
    },
    _getGoodList(country_id) {
      country_id = country_id || ''
      let url = '/Mobile/Index/goodList.html'
      axios.get(url, {
        params: {
          id: 848,
          country_id: country_id
        }
      })
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            this.dataList = res.data
            this.tabIndex = res.data.category[0].id
            this.$store.commit('LOADING_SHOW', false)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    _getAbroad() {
      let url = '/Mobile/Abroad/lxabroad.html'
      axios.get(url, {
        params: {
          id: 844
        }
      })
        .then((res) => {
          console.log(res)
          // this.$store.commit('LOADING_SHOW', false)
          if (res.status === 200) {
            this.lxqz = res.data.lxqz
            this.lxbb = res.data.lxbb
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    changeTab(num) {
      console.log(num)
      this.tabIndex = num
      let sliderName = 'slider' + num
      if (this.sliderName.indexOf(sliderName) < 0) {
        this.sliderName.push(sliderName)
        console.log(sliderName)
        this.$refs[sliderName].showAlert()
      }
    },
    changeTab(num) {
      console.log(num)
      this.tabIndex = num
      let sliderName = 'slider' + num
      if (this.sliderName.indexOf(sliderName) < 0) {
        this.sliderName.push(sliderName)
        console.log(sliderName)
        //this.$refs[sliderName].showAlert()
      }
      /* console.log(this.$refs.sliderTab)
      this.$refs.sliderTab.showAlert() */
    }
  },
  components: {
    pubfooter,
    slideryouxue,
    sliderbigyx,
    topsearch
  }
}



